import React, {useState, useEffect} from 'react'
import { useNavigate } from 'react-router-dom'
import { app } from '../firebaseConfig'
import {getDatabase, ref, get, remove} from 'firebase/database'

const Read = () => {
  const [employees, setEmployees] = useState([])
  const navigate = useNavigate()
  const handleRead = async() =>{
    const db = getDatabase(app)
    const dbRef = ref(db, "employee/employeeInfo")
    const snapshot = await get(dbRef)
    if(snapshot.exists()){
      const myData = snapshot.val()
      const temporaryArray = Object.keys(myData).map((myEmployeeId) =>{
        return {
          ...myData[myEmployeeId],
          employeeId: myEmployeeId
        }
      })

      setEmployees(temporaryArray)
    }
  }

  useEffect(() => {
    handleRead();
  }, []);

  const handleDelete = async(employeeIdParam) =>{
    const db = getDatabase(app)
    const dbRef = ref(db, "employee/employeeInfo/" + employeeIdParam)
    await remove(dbRef)
    window.location.reload()
  }
  return (
    <div className='create'>
      <h3>Read operation</h3>
      {/* <button onClick={handleRead}>Display Data</button> */}
      {employees.length === 0 ? (<p>Oops! It looks like there are no employee records in the database yet.</p>) : (
          <table>
            <thead>
              <tr>
                <td>Employee Id</td>
                <td>Employee Name</td>
                <td>Employee Gender</td>
                <td>Update</td>
                <td>Delete</td>
              </tr>
            </thead>
            <tbody>
              {
                employees.map((employee, index) => (
                  <tr key={index}>
                    <td>{employee.employeeId}</td>
                    <td>{employee.employeeName}</td>
                    <td>{employee.employeeGender}</td>
                    <td><button onClick={() =>{navigate(`/update/${employee.employeeId}`)}}>Update</button></td>
                    <td><button onClick={() => handleDelete(employee.employeeId)}>Delete</button></td>
                  </tr>
                ))
              }
            </tbody>
          </table>
        )
      }
      
    </div>
  )
}

export default Read


