import React from 'react'
import {NavLink} from 'react-router-dom'

const Navbar = () => {
  return (
    <nav className="navbar">
      <h1 className="navbar-title">React Firebase CRUD Operation</h1>
      <NavLink className="link" to="/" exact activeClassName="active">Create</NavLink>
      {/* <NavLink className="link" to="/read" activeClassName="active">Read</NavLink> */}
      <NavLink className="link" to="/read" activeClassName="active">Read</NavLink>
      <NavLink className="link" to="/update" activeClassName="active">Update</NavLink>
    </nav>
  )
}

export default Navbar
