import React, {useState, useEffect} from 'react'
import { useParams } from 'react-router-dom'
import { app } from '../firebaseConfig'
import {getDatabase, ref, set, get} from 'firebase/database'

const Update = () => {
    const {firebaseId} = useParams()

    const [inputValue1, setInputValue1] = useState("")
    const [inputValue2, setInputValue2] = useState("")

    useEffect(() =>{
        const fetchData = async() =>{
            const db = getDatabase(app)
            const dbRef = ref(db, "employee/employeeInfo/"+ firebaseId)
            const snapshot = await get(dbRef)
            if(snapshot.exists()){
                const targetObject = snapshot.val()
                setInputValue1(targetObject.employeeName)
                setInputValue2(targetObject.employeeGender)
                console.log(db)
                console.log(dbRef)
                console.log(snapshot)
                console.log(targetObject)
            } else {
            alert("error")
            }
        }
        fetchData()
    }, [firebaseId])

    const overWriteData = async() =>{
        const db = getDatabase(app)
        const newDataRef = ref(db, "employee/employeeInfo/"+ firebaseId)
        set(newDataRef, {
            employeeName: inputValue1,
            employeeGender: inputValue2
        }).then(()=>{
            alert('data update successfully')
            setInputValue1("")
            setInputValue2("")
        }).catch((error) =>{
            alert(`error: ${error.message}`)
        })
    }
  return (
    <div className='create'>
      <h3>Update operation </h3>
      <label>
        Employee Name: <input type='text' value={inputValue1} onChange={e => setInputValue1(e.target.value)}/>
      </label>
      <label>Employee Gender: <input type='text' value={inputValue2} onChange={e => setInputValue2(e.target.value)}/></label>
      <button onClick={overWriteData}>Update</button>
    </div>
  )
}

export default Update
